import React from "react";
import {useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getDogs } from "../actions/actions";
import { Link } from "react-router-dom";
import Card from './Card'



export default function Home(){
    const dispatch = useDispatch();
    const totalDog = useSelector((state => state.raza))

    useEffect(()=>{
        dispatch(getDogs())
    }, [dispatch])

    function handleClick(e){
        e.preventDefault();
        dispatch(getDogs())
    }

    return (
        <div>
            <Link to={'/CreateRaza'}>
                Create Dogs
            </Link>
            <h1>Aguante Perros Api</h1>
            <button handleClick={e => {handleClick(e)}}>
                Volver a cargar los perros
            </button>
            <div className="filtrado">
                <div className="ascYdes">
                    <select name="orden" id="ascOdes">
                        <option value="asc"> A - Z</option>
                        <option value="des"> Z - A</option>
                    </select>
                </div>
                <div className="peso">
                    <select name="peso" id="select-peso">
                        <option value="peso">Peso</option>
                    </select>
                </div>
                <div className="temperamento">
                    <select name="temperamento" id="select-temperamento">
                        <option value="temperamento">Temperamento</option>
                    </select>
                </div>
                <div className="raza">
                    <select name="raza" id="select-raza">
                        <option value="raza">Raza</option>
                    </select>
                </div>
                <div>
                    {
                      totalDog && totalDog?.map(d=>
                        <Card name={d.name} image={d.img} temperamento={d.temperamento}/>
                      )}
                </div>
            </div>
        </div>
    )
}