import './App.css';
import {BrowserRouter, Route, Routes} from 'react-router-dom';
import Inicial from './components/Inicial'
import Home from './components/Home';

function App() {
  return (
    <BrowserRouter>
    <div className="App">
    <Routes>
        <Route exact path='/' component={Inicial}/>
        <Route exact path='/home' component={Home}/>
    </Routes>  
    </div>
    </BrowserRouter>
  );
}

export default App;
